.App {
  width: 50%;
  margin: 0 auto;
  padding: 15px;
}

.SelectorContainer {
  display: flex;
  align-items: center;
  gap: 15px;
}

.SummaryContainer {
  display: flex;
  align-items: center;
  gap: 15px;
}

h1 {
  margin-bottom: 15px;
}

@media (max-width: 900px) {
  .App {
    width: 90%;
  }
}

.endDate {
  background-color: #f7eefa;
  margin: auto 0;
  padding: 10px;
  border-radius: 5px;
  color: rgb(19, 19, 19);
  filter: drop-shadow(4px 5px 0 black);
  border: 1px solid black;
}
